import React from 'react';
import { getColor } from '../utils/getColor';

const ManaCircleSVG = ({ x, y, civilizations }) => {
    const circleRadius = 7; // 円の半径
    const circleCircumference = 2 * Math.PI * circleRadius; // 円周の長さ

    const getStrokeDashArray = () => {
        const civCount = civilizations.length;
        const dashLength = circleCircumference / civCount; // 各文明に割り当てられる線の長さ
        return `${dashLength} ${circleCircumference - dashLength}`;
    };

    const getCivilizationSegments = () => {
        const civCount = civilizations.length;
        const dashLength = circleCircumference / civCount;

        // 文明の数が偶数なら45度回転、奇数なら-90度
        const rotationAngle = civCount % 2 === 0 ? 45 : -90;

        return civilizations.map((civ, index) => {
            const offset = -index * dashLength; // 各文明のオフセットを計算
            return (
                <circle
                    key={civ}
                    cx="17.5" // 中心X座標
                    cy="17.5" // 中心Y座標
                    r={circleRadius} // 半径
                    fill="none" // 塗りつぶしなし
                    stroke={getColor(civ)} // 文明の色
                    strokeWidth="15" // 円の太さ（縁取りを見せるために黒い円より小さく）
                    strokeDasharray={getStrokeDashArray()} // 文明ごとに線を等分
                    strokeDashoffset={offset} // 文明の開始位置
                    transform={`rotate(${rotationAngle} 17.5 17.5)`} // 回転
                />
            );
        });
    };

    // マナの数字を決定（文明が5つの場合は0、それ以外は1）
    const manaNumber = civilizations.length === 5 ? '0' : '1';

    return (
        <g transform={`translate(${x}, ${y})`}>
            {/* 黒い円（文明が1つの場合は小さい円、2つ以上の場合は大きな円） */}
            <circle
                cx="17.5"
                cy="17.5"
                r={circleRadius}
                fill="none"
                stroke="black"
                strokeWidth={civilizations.length >= 2 ? "23.5" : "16.5"} // 文明が1つの場合は小さい円
            />
            {/* 文明が2つ以上の場合にアーチ状の文字列を表示 */}
            {civilizations.length >= 2 && (
                <>
                    {/* パスの定義（パスの位置を調整） */}
                    <defs>
                        <path
                            id="manaCirclePath"
                            d={`
                                M ${17.5 - (circleRadius + 3)} ${17.5} 
                                A ${circleRadius + 3} ${circleRadius + 3} 0 1 1 ${17.5 + (circleRadius + 3)} ${17.5}
                            `}
                        />
                    </defs>
                    {/* アーチ状の文字列 */}
                    <text
                        fontSize="3.3"
                        fill="white"
                        fontFamily="LINESeed"
                        dy="-5" // テキストを少し上に移動
                    >
                        <textPath
                            href="#manaCirclePath"
                            startOffset="0%"
                        >
                            タップしてマナにおく
                        </textPath>
                    </text>
                </>
            )}
            {/* 文明ごとのセグメントを描画 */}
            {getCivilizationSegments()}
            {/* マナの数字（180度回転して上下逆向きに表示） */}
            {/* マナの数字（180度回転して上下逆向きに表示、黒い縁取り） */}
            {/* 黒い縁取りの外側のテキスト */}
            <text
                x="17.5"
                y="22"
                transform="rotate(180 17.5 17.5)"
                fontSize="16"
                fontWeight="bold"
                stroke="black"
                strokeWidth="2"
                fill="none" // 塗りつぶしなし
                textAnchor="middle"
                fontFamily="LineSeedJP"
            >
                {manaNumber}
            </text>
            {/* 内側の白いテキスト */}
            <text
                x="17.5"
                y="22"
                transform="rotate(180 17.5 17.5)"
                fontSize="16"
                fontWeight="bold"
                fill="white"
                textAnchor="middle"
                fontFamily="LineSeedJP"
            >
                {manaNumber}
            </text>
        </g>
    );
};

export default ManaCircleSVG;
