export const getColor = (civ) => {
    switch (civ) {
        case '火':
            return '#DF392C';
        case '水':
            return '#3EB3E9';
        case '光':
            return '#FDE965';
        case '闇':
            return '#A7A3A0';
        case '自然':
            return '#39AF37';
        case 'ゼロ':
            return '#F5F5F5';
        default:
            return '#FFFFFF';
    }
};
