import React from 'react';
import CostCircleSVG from './CostCircleSVG';
import ManaCircleSVG from './ManaCircleSVG';
import CardTypeFrameSVG from './CardTypeFrameSVG';
import PowerNumberSVG from './PowerNumberSVG';
import CardNameSVG from './CardNameSVG';
import FramePartsSVG from './FramePartsSVG';
import CardTextSVG from './CardTextSVG';
import frameImage from '../assets/parts/dm4.svg';

const CardFrameSVG = ({
    imageUrl,
    cardName,
    race,
    civilization,
    cost,
    power,
    effectText,
    flavorText,
    cardType,
    rubyText,
    cardNameFontSize, // カード名のフォントサイズ
    rubyTextFontSize, // ルビテキストのフォントサイズ
    effectTextFontSize, // 効果テキストのフォントサイズ
    flavorTextFontSize // フレーバーテキストのフォントサイズ
}) => {
    return (
        <svg
            width="252"
            height="344"
            viewBox="0 0 252 344"
            xmlns="http://www.w3.org/2000/svg"
        >
            {/* カードのパーツ要素　下に行くほどレイヤーが上になる */}
            {/* カード画像 */}
            <image href={imageUrl} x="0" y="0" width="252" height="344" />

            {/* フレームパーツ（進化マークなど） */}
            <FramePartsSVG cardType={cardType} />

            {/* カードタイプフレーム */}
            <CardTypeFrameSVG
                x={8.5}
                y={200}
                cardType={cardType}
                civilizations={civilization}
            />

            {/* カードフレーム画像 */}
            <image href={frameImage} x="0" y="0" width="252" height="344" />

            {/* コストサークル */}
            <CostCircleSVG x={7} y={7} cost={cost} civilizations={civilization} />

            {/* カード名と種族 */}
            <CardNameSVG
                x={134}
                y={20}
                cardName={cardName}
                rubyText={rubyText}
                race={race}
                cardNameFontSize={cardNameFontSize} // フォントサイズを渡す
                rubyTextFontSize={rubyTextFontSize}  // ルビテキストのフォントサイズを渡す
            />

            {/* 効果テキストとフレーバーテキスト */}
            <CardTextSVG
                x={20}
                y={220}
                effectText={effectText}
                flavorText={flavorText}
                effectTextFontSize={effectTextFontSize} // 効果テキストのフォントサイズを渡す
                flavorTextFontSize={flavorTextFontSize} // フレーバーテキストのフォントサイズを渡す
            />

            {/* パワー */}
            <PowerNumberSVG number={power} x={20} y={328} />

            {/* マナサークル */}
            <ManaCircleSVG x={108.3} y={308} civilizations={civilization} />
        </svg>
    );
};

export default CardFrameSVG;
