import React from 'react';
import { getColor } from '../utils/getColor';
import CostNumberSVG from './CostNumberSVG';

const CostCircleSVG = ({ x, y, cost, civilizations }) => {
    const circleRadius = 8; // 円の半径
    const circleCircumference = 2 * Math.PI * circleRadius; // 円周の長さ

    const getStrokeDashArray = () => {
        const civCount = civilizations.length;
        const dashLength = circleCircumference / civCount; // 各文明に割り当てられる線の長さ
        return `${dashLength} ${circleCircumference - dashLength}`;
    };

    const getCivilizationSegments = () => {
        const civCount = civilizations.length;
        const dashLength = circleCircumference / civCount;

        // 文明の数が偶数なら45度回転
        const rotationAngle = civCount % 2 === 0 ? 45 : -90;

        return civilizations.map((civ, index) => {
            const offset = -index * dashLength; // 各文明のオフセットを計算
            return (
                <circle
                    key={civ}
                    cx="17" // 中心X座標
                    cy="17" // 中心Y座標
                    r={circleRadius} // 半径
                    fill="none" // 塗りつぶしなし
                    stroke={getColor(civ)} // 文明の色
                    strokeWidth="16" // 円の太さ（縁取りを見せるために黒い円より小さく）
                    strokeDasharray={getStrokeDashArray()} // 文明ごとに線を等分
                    strokeDashoffset={offset} // 文明の開始位置
                    transform={`rotate(${rotationAngle} 17 17)`} // 45度または-90度に回転
                />
            );
        });
    };

    return (
        <g transform={`translate(${x}, ${y})`}>
            {/* 黒い縁取りの円 */}
            <circle
                cx="17"
                cy="17"
                r={circleRadius}
                fill="black"
                stroke="black"
                strokeWidth="18" // 円の直径と同じにして円全体をカバー
            />
            {/* 文明ごとのセグメントを描画 */}
            {getCivilizationSegments()}
            {/* コストの数字 */}
            <CostNumberSVG number={cost} x={17} y={17} />
        </g>
    );
};

export default CostCircleSVG;
